
import {defineComponent} from 'vue'
import Calendar from '@/components/schedule/Calendar.vue'
export default defineComponent({
    components:{
        Calendar,
    },
    emits:[
        'nextStep'
    ],
    setup(props,{emit}){
        function next () {
            emit('nextStep')
        }
        return{
            //methods
            next,
        }
    }
})
